/*---DIAPORAMA HOMEPAGE TEXTE DYNAMIQUE  PANORAMIQUE----*/

// #############################
// Layout avec body.conteneur
// #############################

//Layout du diaporama dynamique (Views slideshow)
//Code imbriqué à 100%
body .conteneur .HPDiapo{
    aside.DiapoPanoHP{
        #transparency{
            background:none repeat scroll 0 0 $rouge;/*Couleur du fond*/
            height:em(65px);
            bottom:em(10px);
            left:em(12px);
            position:absolute;
            width:100%;
            z-index:15;
            @include opacity (0);//Pas de couleur de fond pour le texte en surimpression
        }
        #contenu-diapo{
            position:relative;
            bottom: 10%;
            left: em(10px);
            color: white;
            z-index:20;
            top: em(-85px);
            h2,p{
                color: white;
                padding:em(5px);
                font-family: $AristaFont;
                font-size: auto;
                a{
                    color: white;
                    text-decoration: none;
                }
            }
        }
        img{@include adaptable-img;}
        @include breakpoint($tablet) {
            #transparency{display: block;}
            #contenu-diapo{display: block;}

        }
        @include breakpoint($desktop) { 
            @include clearfix;
            max-width: container($desktopcol);
            #transparency{display: block;}
            #contenu-diapo{display: block;}
            h2,p{
                font-size: larger;
            }
        }
    }
}

// Style du diaporama dynamique avec Overlay // Pas affiché sur Mobile
body .conteneur .HPDiapo .field-content{
  
}
body .conteneur .HPDiapo,
body .conteneur .HPDiapoVerticalDroite
{
    aside.DiapoDroiteHP{
   // display: none; //decommenter pour ne pas afficher sur mobile
   /*@include breakpoint(($mobile,$desktop,$tablet)){ //commenter pour ne pas afficher sur mobile*/
    @include breakpoint ($desktop){
        display: block;
        #transparency{
            height:em(60px);
            bottom:em(69px); 
            left:em(19px);
            width: 98%;
            position:relative;
            z-index:15;
            @include rgba-background($rose-fushia,0.6);/*Couleur et opacité du fond*/
        }
        #contenu-diapo {

            position: relative;
            bottom:8.375em;
            left: em(10px);
            color: white;
            z-index: 20;

            h2,p {
                color: white;
                padding: em(5px);
                font-family: $AristaFont;
                line-height: 1.3em;
            }
            a {
                text-decoration: none;
            }
            p{
                margin-top: 0.2em;
                font-size: x-large;//taille du texte cartouche
            }
        }
    }
    }
}
