/*######-------- ||| ORDRE FLEXBOX ||| --------######*/
body .conteneur .conteneur-flex{
    #colonne-1.page-lycee {order:1;}
    #colonne-1.page-lycee span{display: none;}
    #colonne-2.page-lycee {order:1;}
    #colonne-3.page-lycee {order:2;}
    @include breakpoint ($desktop){
        #colonne-1.page-lycee {order:1;}
        #colonne-1.page-lycee span{display: initial;}
        #colonne-2.page-lycee {order:2;}
        #colonne-3.page-lycee {order:3;}
    }
}

/*######-------- ||| BORDURES ||| --------######*/
body .conteneur{
    @include breakpoint ($desktop){
        #colonne-1.page-lycee{@include border-R-rouge;}
    }

}
/*######-------- ||| BREAKPOINTS ||| --------######*/
@include breakpoint ($mobile){
    aside.deco-lycee{
        display: none;
    }
}

/*######-------- ||| STYLE ||| --------######*/
h1.titre_page_lycee,h1.title-classes{
    font-size: em(24px);
    color: $violet;
    line-height: 1.0em;
    text-align:center;        
}

/*¤¤¤ STYLES COMMUNS ¤¤¤*/
#colonne-1.page-lycee,
#colonne-2.page-lycee,
#colonne-3.page-lycee{
    p{
        padding: em(5px);
    }
    span{
        font-size: small;
        padding: em(5px);
    }
    strong,b{
        color: $violet;
    }
    a{
        color: $violet;
        text-decoration: none;
        &:hover{
            text-decoration: underline;
        }

    }

}

/*------------------------------------
        ¤¤¤ STYLES PROPRES ¤¤¤
______________________________*/
#colonne-1.page-lycee{
