        display: block;
        margin: 0 auto;
        @include adaptable-img;
        width: auto;
        float: none;
        border: em(2px) solid $rouge;
        padding: em(2px);
    }
    div.illus-lycee{
        img{

            transform: scale(0.8);
        }
    }
    a{
        &:hover{}
    }

    h2 a{}
    h2 a:hover{}
    h3 a{}
    h3 a:hover{}
    h4 a{}
    h4 a:hover{}
    aside.diapo_lycee{
        display: block;
        margin: 0 auto;
        width: em(175px) !important;
        width: em(174px);/*IE*/
        * {//cible le contenu
           position:relative;
           /*position:relative !important;*/
           min-height: 100%;
        }
        .views_slideshow_cycle_main{
            /*position:relative !important;*/


        }
        .views_slideshow_cycle_slide{
            overflow: auto;
        }
        img{
            border: em(2px) solid $violet;
            margin: em(10px) 0 0;
        }
    }
    #media-lycee{
        width: 60%;
        margin: em(20px) auto;
        @include breakpoint ($desktop){
            position: relative;
            padding-bottom: 56.25%;
            padding-top: em(85px);
            height: 0;
            overflow: initial;
        }
        iframe,object{
            border: em(2px) solid $violet;
            padding: em(1px);
            @include img-centered;
            max-width: 95%;

            @include breakpoint ($desktop){
                position: absolute;
                top:0;
                left: 0;
                width: 100%;
                height: 100%;

            }
        }


    }

    div.content{
        //style pour le conteneur d'une iframe via JS

        p.external-content,#media-lycee{
            width: 95%;
            margin: em(15px) auto;
            @include breakpoint ($desktop){
                position: relative;
                padding-bottom: 56.25%;
                padding-top: em(85px);
                height: 0;
                overflow: initial;
            }
        }
        //style particulier pour le 1er § qui ne contient pas la iframe 
        > div,p,p:first-child {
            margin: 0 em(15px);
            padding: 0;
        }
        iframe,object{
            border: em(2px) solid $violet;
            padding: em(1px);
            @include img-centered;
            max-width: 95%;

            @include breakpoint ($desktop){
                position: absolute;
                top:0;
                left: 0;
                width: 100%;
                height: 100%;

            }
        }
    }
    /*TABLES STYLES DIVERS*/
    .table_generik{
        border: 0px solid black;width: 75%;margin: 0 auto;
        tbody{
            border: 0;
        }
        tr{
            border: 0px solid black;
        }
        tr.ligne_1{
            background: transparent;
            color: black;
        }
        tr.ligne_2{
            background: $vert;
            color: white;
        }
        td{
            border: 0px solid black;
            font-size: small;
        }
    }

    #table_frais{
        border: 0;
        width: 100%;
    }
    tr.tf_line_1{
        color: white;
        background: #01AAD3;
    }
    tr.tf_line_2{
        color: #E85231;
        background: white;
    }
    /*style lien en savoir plus*/
    li.node_read_more a{
        color : red;
        text-decoration: none;
        text-align:left;
    }
    li.node_read_more a:hover{
        text-decoration: underline;
    }
} 
