/***********************************
    PAGE CLASSE - TABLE
************************************
*/
h1.titre_classes{
    color: $rouge;
    margin: 5px;
}
#colonne-1.page-lycee{
    #table-classes{
        margin: em(10px);
        tbody{
            border: 0;
            background: 0;
        }
        tr{
            border: 0;
            background: 0;
        }
        td{
            text-align: center;
            background: $gris-clair;
            display: inline-block;
            margin: em(2px) em(7px);
            width: auto;
            vertical-align: text-top;
        }
        td:hover {
            -moz-box-shadow: 0 0 2px #000000;
            -webkit-box-shadow: 0 0 2px #000000;
            box-shadow: 0 0 2px #000000;
        }
        td a{
            color: $gris-anthracite;
            display: block;
            margin: em(2px) 0;
            padding: em(3px);
            text-decoration: none;
        }
        td a:hover{
            background: $rouge;
            color: white;
        }
        .filefield-icon{
            display: none;
        }

        h3 {
            font-size: small;
            color: $bleu-turquoise;
            display: block;
            font-family: $AristaFont;
            padding: em(5px);
        }
    }
}
/***********************************
    PAGE CLASSE - LISTE
************************************/
#colonne-1.page-lycee{
    div.enveloppe-classes{
 @include span($mobilecol);
       
        @include clearfix;
        @include breakpoint ($tablet){
            @include span(8 of $tabletcol);
            
        }
        @include breakpoint ($desktop){
           @include span(7 of 8);//car inclus dans une zone de 8 colonnes
           @include isolate(-1.3em);
            @include gutters(.3em);/*Toujours en dernier pour override de la taille des gouttieres*/
        }
        //Pour orientation paysage
         @include breakpoint ($tabletlandscape){
           @include span(8 of $tabletcol-landcsape);
            
        }
    }
    ul.liste-classe{
               
        //Layout de la liste en galerie
        li.classe-item{
            @include gallery(3 of $mobilecol);

            @include clearfix;
            @include breakpoint ($tablet){
                @include gallery(3 of $tabletcol);

            }
            @include breakpoint ($desktop){
                @include gallery(4 of $desktopcol);//3 items par lignes
            }
            //Pour orientation paysage
            @include breakpoint ($tabletlandscape){
                @include gallery(4 of $tabletcol-landcsape);
            }
        }
    }
    //Style de la liste en galerie
    ul.liste-classe{

        li{
            list-style:none;
            /*min-width: 33%;*/ 
            text-align: center;
            background: $gris-clair;
            
            margin: em(7px) em(7px);
            vertical-align: text-top;
            &:hover {
                @include ombrage-bloc(0, 0, em(3px), #555);
            
            }
            a{
                color: $gris-anthracite;
                display: block;
                margin: em(2px) 0;
                padding: em(3px);
                text-decoration: none;
                &:hover{
                    background: $rouge;
                    color: white;
                }
            }

            span.titre-classe{
                font-size: medium;
                color: $bleu-turquoise;
                display: block;
                font-family: $AristaFont;
                padding: em(5px);
            }
            .filefield-icon{
                display: none;
            }
        }
    }
}
